  @media screen and (max-width: 1086px) {
    .nav::before {
      content: "\f0c9";
      font-family: "font awesome 5 free";
      font-weight: bold;
      margin-left: 10px;
      font-size: 30px;
      display: flex;
      justify-content: center;
      width: 2rem;
      height: 2rem;
      border-radius: 5px;
      background-color: #d3cfd3d5;
      position: absolute;
      bottom: -185px;
    }
   
    .first-nav{
      flex-direction: column;
      width: 25%;
      display: none;

    }
    .second-nav{
      
      width: 25%;
      flex-direction: column;
    }
    .nav{
      flex-direction: column;
      position: relative;
      gap: 10px;
      top: -200px;
      left: -200px;
    } 
    
    #zoom .first-nav div, #zoom .second-nav div{
      padding: 5px 10px;
      flex-direction: row;
      justify-content: space-between;
      /* display: none; */
  
    }
    .nav:hover .nav-child{
