* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#zoom {
  width: 100%;
  min-height: 100vh;
  color: #fff;
  background-color: #000;
  grid-template-rows: 30px 1fr 40px;
  gap: 5px;
  padding-bottom: 20px;
  display: grid;
  overflow: hidden;
}

#zoom header {
  text-align: center;
  color: #312e2e;
  background-color: #fff;
  padding: 5px;
}

#zoom section {
  width: 90%;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  margin: auto;
  display: flex;
}

#zoom section div {
  width: 300px;
  height: 18vh;
  background-color: #ffffff73;
  border-radius: 5px;
  align-items: flex-end;
  display: flex;
  position: relative;
}

#zoom section div h4 {
  background-color: #00000092;
  border-radius: 5px;
  margin: 5px;
  padding: 2px 5px;
  font-weight: 300;
}

#zoom section div h4:before {
  content: "";
  color: red;
  margin-right: 5px;
  font-family: "font awesome 5 free";
  font-weight: 600;
}

#zoom section a {
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
}

#zoom section div:hover {
  z-index: 2;
  border: 2px solid #fff;
  transform: scale(1.05);
}

#zoom nav {
  height: 60px;
  background-color: #302f2f;
  justify-content: space-between;
  align-items: center;
  margin-top: 5px;
  font-size: 90%;
  display: flex;
}

.nav {
  width: 60%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#zoom nav div {
  height: 100%;
}

#zoom .first-nav {
  width: 15%;
  justify-content: space-around;
  display: flex;
}

#zoom .first-nav div {
  width: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

#zoom .first-nav div:nth-child(1) i {
  color: red;
}

#zoom .second-nav {
  width: 35%;
  display: flex;
}

#zoom .second-nav div {
  width: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

#zoom nav button {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px 20px;
}

#zoom .first-nav div:hover, #zoom .second-nav div:hover {
  background-color: #554f4f;
  border-radius: 5px;
}

#zoom section div:nth-child(2) {
  background-image: url("chandu.a77349fa.jpg");
  background-size: cover;
}

#zoom section div:nth-child(3) {
  background-image: url("francis.69a4d8f5.jpg");
  background-position: top;
  background-size: cover;
}

#zoom section div:nth-child(4) {
  background-image: url("teng.26bed739.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}

#zoom section div:nth-child(7) {
  background-image: url("vijaya.c205ebcd.jpg");
  background-position: bottom;
  background-size: cover;
}

#zoom section div:nth-child(8) {
  background-image: url("agartha.4665251b.jpg");
  background-position: 0 -40px;
  background-repeat: no-repeat;
  background-size: cover;
}

#zoom section div:nth-child(10) {
  background-image: url("rana.fc0e4b7d.jpg");
  background-position: center;
  background-size: cover;
}

#zoom section div:nth-child(12) {
  background-image: url("janos.d2fd149f.jpeg");
  background-size: cover;
}

#zoom section div:nth-child(16) {
  background-image: url("alveena.2660a00f.jpg");
  background-size: cover;
}

#zoom section div:nth-child(18) {
  background-image: url("diouani.8b6506f3.jpg");
  background-position: 100%;
  background-size: cover;
}

@media screen and (max-width: 1086px) {
  .nav:before {
    content: "";
    width: 2rem;
    height: 2rem;
    background-color: #d3cfd3d5;
    border-radius: 5px;
    justify-content: center;
    margin-left: 10px;
    font-family: "font awesome 5 free";
    font-size: 30px;
    font-weight: bold;
    display: flex;
    position: absolute;
    bottom: -185px;
  }

  .first-nav {
    width: 25%;
    flex-direction: column;
    display: none;
  }

  .second-nav {
    width: 25%;
    flex-direction: column;
  }

  .nav {
    flex-direction: column;
    gap: 10px;
    position: relative;
    top: -200px;
    left: -200px;
  }

  #zoom .first-nav div, #zoom .second-nav div {
    flex-direction: row;
    justify-content: space-between;
    padding: 5px 10px;
  }

  .nav:hover .nav-child {
    display: block;
  }
}

